header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
/* CTA */
.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* header socials  */
.header_social{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
}
.header_social::after{
    content: " | ";
    width: 1px;
    height: 5rem;
    color: var(--color-primary);
}

/* me */

.me img{
    background-color: var(--color-primary), cover;
    width: 22rem;
    height: 41rem;
    position: absolute;
    left: calc(50% - 11rem);
    margin-top: 2rem;
    border-radius: 10rem 10rem 0 0 ;
    overflow: hidden;
    padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* social Down */

.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;
}

/* media queries for medium devices */
@media screen  and (max-width: 1024px){
    header{
        height: 80vh;
    }
}

/* media queries for small devices */
@media screen  and (max-width: 600px){
    header{
        height: 100vh;
    }
    .header_social,.scroll_down{
        display: none;
    }
}